body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.extend-link {
  text-decoration: none;
  cursor: pointer; }

.extend-link:hover {
  color: #0056b3;
  cursor: pointer;
  text-decoration: none; }

.clearfix {
  zoom: 1; }
  .clearfix:before, .clearfix:after {
    content: "";
    display: table; }
  .clearfix:after {
    clear: both;
    visibility: hidden;
    font-size: 0;
    height: 0; }

body {
  color: #000;
  font-size: 14px;
  margin: 0;
  padding: 0;
  font-family: CeraPro,PingFangSC-Regular,PingFang SC !important; }

.nav-link {
  padding: 0;
  cursor: pointer; }

.news-link {
  color: #000;
  color: initial; }
  .news-link:hover {
    color: #000;
    color: initial; }

@font-face {
  font-family: CeraPro;
  src: url(/static/media/cerapro-light.250de40e.ttf);
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: CeraPro;
  src: url(/static/media/CeraPro.ea9ac4a6.ttf);
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: CeraPro;
  src: url(/static/media/cerapro_bold.5390828f.ttf);
  font-weight: bold;
  font-style: normal; }

.navbar-toggler {
  border: none; }

.navbar-light .navbar-toggler-icon {
  color: #000; }

.cs-header .dropdown-toggle::after {
  display: none; }

.navbar-light .navbar-toggler-icon {
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACQAAAAgCAYAAAB6kdqOAAAAlklEQVRYR+2YQQqAIBBFnzdoUYtOm53WRS06QkRKBNG0ESf4AyIy4nz+fB01cFqXWx426TZgCzl0BKYmMK6gMxAF6CULvhkagaO1tASkoqGWQG6xBchKhRgSQxYDlr9oqAcGa3Jl/wKsqmW/rWXu7kPuRF15A31fXqXD4koMiSGLActfNOTuku/upBagBy35frm6+Y7ZAXL8JCEk5kTfAAAAAElFTkSuQmCC) top center no-repeat;
  background-size: 18px 16px;
  width: 18px;
  height: 16px; }

.overlay--active {
  pointer-events: auto;
  touch-action: none; }

.overlay {
  bottom: 0;
  left: 0;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
  z-index: 5;
  color: rgba(0, 0, 0, 0.87); }

.overlay:before {
  background-color: #212121;
  bottom: 0;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: inherit;
  transition-delay: .15s;
  width: 100%; }

.overlay--active:before {
  opacity: .7; }

.spacer {
  flex-grow: 1 !important; }

.dropdown-menu {
  padding: 1rem 0;
  border: none;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08);
  text-align: center;
  left: -30px; }

.dropdown-item {
  padding: 0.55rem 1.5rem; }

.row > div {
  padding: 0; }

@media (min-width: 1200px) {
  .container {
    max-width: 1170px; } }

